// const baseUrl = 'http://localhost:8089/'
const baseUrl = 'https://read.youngbird97.top/'
const pathArr = ['pages/index/index','pages/shop/shop/shop','pages/shop/product_detail/product_detail',
'pages/shop/cart/cart','pages/user/index/index',"pages/auth/login/login"]; // 
// const baseUrl = 'https://read.youngbird97.top/'
let isRefreshing = true; // 
let pendings = []; // 
// const loginPage = "/pages/auth/login/login";
const loginPage = "/pages/user/index/index";
const showToast = (title) => {
    uni.showToast({
        title: title,
        icon: 'none'
    })
}

/**
 * @method http
 * @param {String} url 
 * @param {Object} data 
 * @param {Object} option 
 * @return {Object} Promise
 */
const http = (url, data = {}, option = {}) => {
    let hideLoading = option.hideLoading || false //  loading
    let hideMsg = option.hideMsg || false // 
	let token =  uni.getStorageSync('Token') || '';
	const pages = getCurrentPages();
	const router = pages[pages.length - 1]['route']; // 
	option.needLogin = true;
	if (pathArr.indexOf(router) != -1) option.needLogin = false; // 
	
	//   token 
	if (token === '' && option.needLogin) {
		getApp().globalData.hasLogin = false;
		uni.reLaunch({
			url: loginPage,
		})
		return
	}
	
    if (!hideLoading) {
        uni.showLoading({
            title: '...',
            mask: true
        })
    }
    return new Promise((resolve, reject) => {
        uni.request({
            url: baseUrl + url,
            method: option.method || 'POST', //  post 
            header: {
                'Token': token,
            },
            data: data,
            success: res => { // 
                if (!hideLoading) uni.hideLoading()
				
				
                if (res.statusCode === 200 || res.statusCode === 201) {
                    let result = res.data
					console.info(result)
					if(result.code === -1){
						// 
						showToast(result.msg )
						setTimeout(function() {
							uni.reLaunch({
								url: loginPage
							})
						}, 2000);
						return
					}
                    if (result.code === 200 || result.code === 404 || result.code === 40164) {
                        resolve(result)
                        return
                    }
                    reject(result.errmsg)
                    if (!hideMsg) showToast(result.errmsg)
                } else { //  200
                    showToast('[' + res.statusCode + '] ')
                    reject('[' + res.statusCode + '] ')
                }
            },
            fail: (err) => { // 
				console.info(err)
                if (!hideLoading) uni.hideLoading()
                if (err.errMsg != 'request:fail abort') {
                    showToast('')
                    reject('')
                }
            }
        })
    })
}


//
export const wxLogin = (entUser) => {
	return new Promise((resolve, reject) => {
		let myApp = this;
		uni.login({
			provider: 'weixin',
			success: function(res) {
				console.info("weixinLogin"+res);
				http('wx/login', {
					'code': res.code,
					'encryptedData':entUser.encryptedData,
					'iv':entUser.iv,
					'signature':entUser.signature,
					'avatarUrl':myApp.customer.avatarUrl 
				}, {
					hideLoading: false, //  false
					hideMsg: true, //  false
					method: 'POST' //  POST
				}).then(res => {
					myApp.hasLogin = true;
					//openid
					uni.setStorage({
						key: 'customer',
						data: res.data,
						success: function () {
							myApp.customer = res.data;
						},
					});
					uni.setStorageSync('Token',res.data.token);
				
				}).catch(err => {
					console.error(err)
				})
			}	
			,fail(e) {
				console.log(e);
			}
			,complete(e) {
				console.log(e);
			}
		});

	})
}

export default http
